<template>
  <span class="btn" @click.stop="click">
    <slot></slot>
  </span>
</template>
<script>
export default {
  methods: {
    click(){
        this.$emit('click',arguments)
    }
  }
};
</script>

<style lang="less" scoped>
.btn {
  color: #00aaee;
  cursor: pointer;
}
.btn:hover {
  opacity: 0.8;
}
.btn + .btn {
  margin-left: 15px;
}
</style>